nav {
	height: 53px;
	background-color: #212121; }
.header {
	position: relative; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 14px 70px;
	margin: 0;
	list-style-type: none;
	.menu_link {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 300;
		font-size: 15px;
		color: #FFFFFF; }
	.menu_item {
		border-right: 2px dotted black;
		padding-right: 32px; } }
.subheader {
	position: absolute;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, .6);
	.subheader_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	.subheader_official {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 100;
		font-size: 13px;
		color: #FFFFFF; }
	.subheader_call {
		margin-top: 22px;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 300;
		font-size: 12px;
		color: #FFFFFF;
		text-align: right; }
	.subheader_number {
		display: block;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 400;
		font-size: 22px;
		color: #FFFFFF;
		text-align: right; }
	.subheader_btn {
		margin-top: 19px;
		width: 180px;
		height: 45px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
		border-radius: 4px;
		border: none;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 400;
		font-size: 14px;
		color: #FFFFFF; } }
.promo {
	min-height: 800px;
	background: url(../img/uber_bg.jpg) center center/cover no-repeat;
	padding: 150px 0 135px 0;
	.promo_header {
		margin: 68px auto 0 auto;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 900;
		font-size: 60px;
		color: #FFFFFF; }
	.promo_subheader {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 700;
		font-size: 36px;
		color: #FFFFFF;
		text-align: center; }
	.promo_text {
		margin-top: 16px;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 400;
		font-size: 17.9794px;
		line-height: 24px;
		color: #FFFFFF;
		text-align: center; }
	.promo_btn {
		display: block;
		margin: 114px auto 0 auto;
		width: 250px;
		height: 63px;
		background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0px -3.99543px 0px rgba(0, 0, 0, 0.12);
		border-radius: 4px;
		border: none;
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 400;
		font-size: 18px;
		color: #FFFFFF; } }
.reason {
	padding: 77px 0 66px 0;
	.reason_subheader {
		font-family: 'Roboto';
		font-style: normal;
		font-weight: 700;
		font-size: 38px;
		color: #222222;
		text-align: center; }
	.reason_block {
		display: flex;
		justify-content: center;
		align-items: center;
		.reason_round {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 116px;
			height: 116px;
			background-color: #1EACC7;
			border-radius: 100%; }
		.reason_descr {
			width: 340px;
			padding-left: 24px; }
		.reason_title {
			font-family: 'Roboto';
			font-style: normal;
			font-weight: 400;
			font-size: 17px;
			line-height: 21px;
			color: #1EACC7; }
		.reason_text {
			font-family: 'Roboto';
			font-style: normal;
			font-weight: 400;
			font-size: 16.9806px;
			line-height: 21px;
			color: #202020; } } }


.mobile {
    min-height: 863px;
    padding: 45px 0 79px 0;
    background: url('../img/mobile_bg.png')center center/cover no-repeat;
    .mobile_header {
        Font-family: Roboto;
        Font-style: Bold;
        Font-size: 38px;
        font-weight: 700;
        text-align: center; }
    .mobile_subheader {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        text-align: center; }
    .row {
        margin-top: 32px; }
    &_block {
        position: relative;
        width: 320px;
        height: 280px;
        margin-bottom: 19px;
        &_1 {
            background: url('../img/card1.jpg')center center/cover no-repeat; }
        &_2 {
            background: url('../img/card2.jpg')center center/cover no-repeat; }
        &_3 {
            background: url('../img/card3.jpg')center center/cover no-repeat; }
        &_4 {
            background: url('../img/card4.jpg')center center/cover no-repeat; }
        &_5 {
            background: url('../img/card5.jpg')center center/cover no-repeat; }
        &_6 {
            background: url('../img/card6.jpg')center center/cover no-repeat; } }
    &_descr {
        padding-top: 82px;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        color: #FFFFFF;
        text-align: center; }
    &_plus {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 35px;
        left: 50%;
        transform: translateX(-50%);
        width: 29px;
        height: 29px;
        border-radius: 100%;
        background-color: #000000;
        margin: 0 auto;
        cursor: pointer; } }



.car {
    position: relative;
    min-height: 897px;
    padding: 58px 0 137px 0;
    background: url('../img/mobile_bg.png')center center/cover no-repeat;
    &_label {
        display: block;
        position: relative;
        margin: 0 auto;
        padding-top: 5px;
        width: 115px;
        height: 27px;
        background-color: #FFFFFF;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 11px;
        color: #070716;
        text-align: center;
        z-index: 2; }
    &_header {
        position: relative;
        padding-top: 25px;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 700;
        font-size: 38px;
        text-align: center;
        color: #FFFFFF;
        z-index: 2; }
    &_subheader {
        position: relative;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        text-align: center;
        color: #FFFFFF;
        z-index: 2; }
    &_img {
        position: relative;
        display: block;
        margin: 54px auto 0 auto;
        z-index: 2; }
    &_text {
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 300;
        font-size: 17px;
        text-align: center; }
    .dark {
        position: absolute;
        top: 0;
        left: 0;
        width: 1918px;
        height: 501px;
        background-color: #070716;
        z-index: 1; } }













